<template>
    <div class='portal-body'>
        <my-area v-for='(area,index) in areaList' :key='index' :titleData='area' :areaData='areaData[area.model]'></my-area>
    </div>
</template>
<style>
    .portal-body {
        padding: 30px;
    }

    .card-logo {
        width: 40px;
        height: 40px;
        border-radius: 50%;
    }
</style>
<script>
    import Area from './component/my-area.vue'
    import LingGanCaiJi from '../../data/linggancaiji.json'
    export default {
        components: {
            'my-area': Area
        },
        data() {
            return {
                areaList: [{
                    icon: 'fireball',
                    title: '灵感采集',
                    model: 'LingGanCaiJi'
                }],
                areaData: {
                    LingGanCaiJi
                }
            }
        }
    }

</script>


