<template>
    <Card class="my-card" style="width:320px">
        <Row>
            <Col span="6">
            <img class="my-card-logo" :src="cardData.imgUrl">
            </Col>
            <Col span="18">
            <h4>{{cardData.name}}</h4>
            <p>{{cardData.des.length>15?cardData.des.substr(0,13)+'...':cardData.des}}</p>
            </Col>
        </Row>
    </Card>
</template>
<style>
    .my-card {
        cursor: pointer;
        display: inline-block;
    }

    .my-card-logo {
        width: 40px;
        height: 40px;
        border-radius: 50%;
    }
</style>
<script>
    export default {
        props: ['cardData']
    }

</script>


